<script>
	//import * as d3 from 'd3';
  import { date, dateString, tasks, tasksByClient, totalDuration, formattedTotalDuration } from './store.js'
  import { calcDuration, MStoDecimalHrs, formatDuration }	 from './utils'
  $: hours = $totalDuration/1000/60/60

  function copyTasks(tasks) {
    let taskStr = tasks.reduce((acc, task, i, arr) => acc+`${task.task} (${MStoDecimalHrs(calcDuration($date,task.startTime,task.endTime))}) ${(i<arr.length-1) ? '// ' : ''}`, `${$dateString} - `)
    console.log(taskStr)
    const clipboard = document.createElement('input')
    clipboard.id= 'clipboard'
    clipboard.value = taskStr
    document.body.appendChild(clipboard)
    clipboard.select()
    document.execCommand('copy')
    clipboard.remove()
  }
</script>

<style>
  .main-content {
    margin: 0 auto;
    max-width: 700px;
    min-width: 400px;
    box-sizing: border-box;
    padding: 2vh 3vw;
    border-radius: 2vmin;
    background: inherit;
    box-shadow: 0 .6vh 1.1vh rgba(0,0,0,0.2),
                0 1.2vh 1.9vh rgba(0,0,0,0.08);
  }
  .big-span {
    display: block;
    margin: 0 auto .75em auto;
    font-size: 2em;
    text-align: center;
    color: hsl(190deg, 30%, 50%);
  }
  h4 {
    font-size: 1.5em;
    font-weight: normal;
    margin: 0 auto;
    margin-bottom: .5em;
    padding: .25em 0 .25em 0;
    width: 100%;
    text-align: center;
    position: relative;
    background: transparent;
    transition: all .1s ease-in-out;
  }
  h4:hover {
    background: hsl(190deg, 50%, 40%);
    cursor: pointer;
    color: white;
  }
  h4::after {
    position: absolute;
    content: 'click to copy tasks to clipboard!';
    font-size: .8rem;
    color: white;
    font-weight: 400;
    text-align: right;
    opacity: 0;
    right: 2ch;
    top: 50%;
    transform: translate(0, 0);
    transition: all .11s .05s ease-in-out;
  }
  h4:hover::after {
    opacity: 1;
    transform: translate(0,-50%);
  }

  table {
    margin: 0 auto 1em auto;
    width: 100%;
    border: solid 1px hsl(190deg, 95%, 40%);
    border-collapse: collapse;
  }
  tr:not(:last-child) {
    border-bottom: solid 1px hsl(190deg, 20%, 70%);
  }

  th {
    color: hsl(190deg, 80%, 40%);
    text-align: left;
    font-weight: normal;
    text-transform: uppercase;
  }

  td, th {
    padding: .5em .75em;
  }
</style>

<section class='main-content'>
  <span class="big-span">You worked { $formattedTotalDuration } today{
    (hours > 8) ? (hours < 10) ? '!' : '! GO HOME!' : '.'
  }</span>
  <h3>Tasks by Client</h3>
  {#each $tasksByClient as cat, i}
  <h4 tabindex=0 on:click={() => copyTasks(cat.tasks)}>{cat.client}</h4>
  <table>
    <tbody>
      <tr>
        <th>Task</th>
        <th>Time</th>
      </tr>
      {#each cat.tasks as task}
      <tr>
        <td>{task.task}</td>
        <td>{ formatDuration(calcDuration($date, task.startTime, task.endTime)) }</td>
      </tr>
      {/each}
      <tr>
        <th>Total</th>
        <td>{ formatDuration(cat.tasks.reduce((acc, t) => acc+calcDuration($date, t.startTime, t.endTime), 0)) }</td>
      </tr>
    </tbody>
  </table>
  {/each}
</section>