<script>
	import TaskTable from './TaskTable.svelte'
	import ClientTable from './ClientTable.svelte'
	import { initialTask, date, tasks, getTasks, wkDateString, storageString } from './store.js'	
	let tasksJSONStr = '';

	function clearTasks() { tasks.set(initialTask) }

	// read from memory if the date changes
	$: {
		tasks.set(getTasks($storageString))
	}

	// write to memory if only the tasks change
	$: if ($tasks.dateStr === $storageString) {
		localStorage.setItem($storageString, JSON.stringify($tasks))
	}

	const dayInMS = 1000*60*60*24
	function changeDate(change) {
		date.update(date => { return new Date(date.getTime() + change*dayInMS) } )
	}
</script>

