<script>
	import { date, tasks, storageString } from './store.js'
	import { now, formatTime, calcFormattedDuration } from './utils.js'
	let newTask = createNewTask()

	function createNewTask() {
		return {
			client: '',
			task: '',
			startTime: now(),
			endTime: now(15)
		}
	}

	function addTask() {
		tasks.set({ dateStr: $tasks.dateStr, taskList: [...$tasks.taskList, newTask] })
		newTask = createNewTask()
		document.getElementById('input_client').focus()
	}
	
	function handleFormKeyUp(e) {
		if (e.key === 'Enter') {
			addTask()
		}
	}
	
	function delTask(e) {
		let tasksCopy = { dateStr: $tasks.dateStr, taskList: [...$tasks.taskList]}
		let i = parseInt(e.target.dataset.id)
		tasks.set({ dateStr: tasksCopy.dateStr, taskList: [...tasksCopy.taskList.slice(0, i), ...tasksCopy.taskList.slice(i+1)] })
	}
</script>

<table>
	<tbody>
		<tr>
			<th class='l'>Client</th>
			<th class="l">Task</th>
			<th>Start</th>
			<th>End</th>
			<th>Duration</th>
		</tr>
		{#each $tasks.taskList as task, i (task.task + task.startTime)}
		<tr>
			<td>{ task.client }</td>
			<td>{ task.task }</td>
			<td class='c'>{ formatTime($date, task.startTime) }</td>
			<td class='c'>{ formatTime($date, task.endTime) }</td>
			<td class='c'>{ calcFormattedDuration($date, task.startTime, task.endTime) }</td>
						<button class="btn_del" data-id={i} on:click={delTask}>ðŸ—‘</button>
		</tr>
		{/each}
		<tr>
			<td>
				<input id='input_client' type='text' bind:value={newTask.client} placeholder="Client, LLC"
							 on:keyup={handleFormKeyUp} />
			</td>
			<td>
				<input class='input_task-name' type='text'
							 bind:value={newTask.task} placeholder="Enter a new task"
							 on:keyup={handleFormKeyUp} />
			</td>
			<td class='c'>
				<input type='time' bind:value={newTask.startTime}
							 on:keyup={handleFormKeyUp} />
			</td>
			<td class='c'>
				<input type='time' bind:value={newTask.endTime}
							 on:keyup={handleFormKeyUp} />
			</td>
			<td class='c'>
				<button class="btn_add" on:click={addTask}>Add Task</button>
			</td>
		</tr>
	</tbody>
</table>

<style>
	table {
		margin: 3vh auto;
	}
	
	th {
		font-size: calc(.3em + 2.2vmin);
		padding-bottom: .5em;
		border-bottom: solid 1px hsl(190, 70%, 70%);
	}

	th, td {
		max-width: 25vw;
	}
	
	td {
		font-size: calc(.25em + 1.25vmin);
		padding: .5em 0;
	}
	
	.l {
		text-align: left;
	}
	
	.c {
		text-align: center;
	}
	
	.btn_del {
		position: relative;;
		background: transparent;
		border: solid 2px lightgray;
		border-radius: .3em;
		padding: .2em .3em;
		margin: 0;
		top: 0;
		transition: all .08s ease-in-out;
		opacity: 0;
	}
	tr:hover .btn_del {
		opacity: 1;
	}
	.btn_del:hover {
		background: hsl(20deg, 80%, 80%);
	}
	
	tr input, tr .btn_add {
		margin: 0;
		padding: .25em .5em;
		transition: background .08s ease-in-out;
  }
	tr input {
		display: inline-block;
		max-width: 100%;
		border: none;
		border-bottom: solid 3px hsl(190deg, 70%, 85%);
		border-radius: 0 0 .25em .25em;
		transition: all .09s ease-in-out;
	}
	tr input:not([type='time']) {
		padding: calc(.25em + 2px) .5em;
	}
	tr .btn_add {
		width: 100%;
	}
	tr .btn_add:hover,
	tr .btn_add:focus {
		outline: none;
		background: hsl(190deg, 70%, 90%);
		border-radius: 0 .5em 0 .5em;	
	}
	tr input:hover,
	tr input:focus {
		outline: none;
		border-bottom: solid 3px hsl(190deg, 70%, 75%);
		border-radius: 0 0 .25em .75em;
	}
	
	.c input {
		margin: 0 auto;
	}
	
	.input_task-name {
		width: 50ch;
		max-width: 100%;
	}

	@media(max-width:570px) {
		.btn_del {
			transform: translate(-150%);
			z-index: 5;
			background: hsl(20deg, 80%, 80%);
		}
	}
</style>